<template>
  <div class="hd_settings_box">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="活动主题" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="活动描述" prop="desc">
        <el-input type="textarea" :rows="6" v-model="ruleForm.desc"></el-input>
      </el-form-item>
      <el-form-item label="抽奖背景">
        <el-upload :limit='1' :on-exceed='chaochu' :on-success='success' :action="'https://mail.yidonghuayuan.com/shop/file/uploadimg'"
          list-type="picture-card" :on-preview="handlePictureCardPreview" :on-remove="handleRemove">
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="ruleForm.icon" alt="">
        </el-dialog>
        <p style="color:#aaaaaa;font-size:12px">抽奖背景建议尺寸最佳显示尺寸为（1920*400像素）</p>
      </el-form-item>
      <el-form-item class="animated flipInX">
        <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        ruleForm: {
          name: '',
          desc: '',
          icon:''
        },
        dialogVisible :false,
        rules: {
          name: [{
              required: true,
              message: '请输入活动主题',
              trigger: 'blur'
            },
            {
              min: 3,
              max: 5,
              message: '长度在 3 到 5 个字符',
              trigger: 'blur'
            }
          ],
          desc: [{
            required: true,
            message: '请填写活动描述',
            trigger: 'blur'
          }]
        }
      };
    },
    methods: {
      //上传
      handleRemove(file, fileList) {
        this.ruleForm.icon = '';
      },
      handlePictureCardPreview(file) {
        this.ruleForm.icon = file.url;
        this.dialogVisible = true;
      },
      success(response, file) {
        this.ruleForm.icon = file.response.msg;
      },
      chaochu() {
        this.$message({
          showClose: true,
          message: '最多上传一张图片哦!',
          type: 'warning'
        });
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
    }
  }
</script>

<style scoped>
  .hd_settings_box {
    width: 50%;
    margin-top: 30px;
    /* background: white; */
  }
</style>



